.premium-content-switcher {
    border-color: #000;
    // overflow: hidden;
    padding: 10px 20px;

    .premium-content-switcher-toggle-switch {
        font-size: 15px;
        -webkit-box-align: center;
        -webkit-align-items: center;
        -moz-box-align: center;
        -ms-flex-align: center;
        align-items: center;
    }

    .premium-content-switcher-toggle-switch-label {
        position: relative;
        display: inline-block;
        width: 4.5em;
        height: 2.3em;
        vertical-align: middle;
    }

    .premium-content-switcher-toggle-switch-label input {
        opacity: 0;
        width: 0;
        height: 0;
        display: none;
    }

    .premium-content-switcher-toggle-switch-slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        -webkit-transition: 0.4s;
        transition: 0.4s;
        border-radius: 1.5em;
        background: rgb(242, 242, 242);

        &::before {
            position: absolute;
            content: "";
            height: 1.8em;
            width: 1.8em;
            left: 0.25em;
            bottom: 0.25em;
            -webkit-transition: 0.4s;
            transition: 0.4s;
            -webkit-border-radius: 50%;
            border-radius: 50%;
            -webkit-transform: translateX(0);
            -moz-transform: translateX(0);
            -ms-transform: translateX(0);
            transform: translateX(0);
            background: #793dc3;
        }
    }

    .premium-content-switcher-toggle-switch-label input:checked+.premium-content-switcher-toggle-switch-slider::before {
        transform: translateX(2.1em);
    }

    .premium-content-switcher-toggle-inline {
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
        -webkit-flex-direction: row;
        -moz-box-orient: horizontal;
        -moz-box-direction: normal;
        flex-direction: row;
        -js-display: flex;
        display: -webkit-box;
        display: -webkit-flex;
        display: -moz-box;
        display: -ms-flexbox;
        display: flex;
        border-color: #000;
        margin-bottom: 15px;
    }

    .premium-content-switcher-toggle-block {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -webkit-flex-direction: column;
        -moz-box-orient: vertical;
        -moz-box-direction: normal;
        -js-display: flex;
        display: -webkit-box;
        display: -webkit-flex;
        display: -moz-box;
        display: -ms-flexbox;
        display: flex;
        flex-direction: column;
        border-color: #000;
        margin-bottom: 15px;
    }

    .premium-content-switcher-toggle-inline .premium-content-switcher-first-label {
        margin-right: 2em;
        -js-display: flex;
        display: -webkit-box;
        display: -webkit-flex;
        display: -moz-box;
        display: -ms-flexbox;
        display: flex;
    }

    .premium-content-switcher-toggle-inline {
        .premium-content-switcher-second-label {
            margin-left: 2em;
            -js-display: flex;
            display: -webkit-box;
            display: -webkit-flex;
            display: -moz-box;
            display: -ms-flexbox;
            display: flex;
        }
    }

    .premium-content-switcher-toggle-block {
        .premium-content-switcher-first-label {
            margin-bottom: 1em;
            -js-display: flex;
            display: -webkit-box;
            display: -webkit-flex;
            display: -moz-box;
            display: -ms-flexbox;
            display: flex;
        }
    }

    .premium-content-switcher-toggle-block {
        .premium-content-switcher-second-label {
            margin-top: 1em;
            -js-display: flex;
            display: -webkit-box;
            display: -webkit-flex;
            display: -moz-box;
            display: -ms-flexbox;
            display: flex;
        }
    }

    .premium-content-switcher-inline-editing {
        min-height: 15px;
        margin: 0;
        display: table;
        border-color: #000;
    }

    .premium-content-switcher-two-content {
        position: relative;
        margin: 0 !important;
        padding: 0 !important;
        // overflow: hidden;
        list-style-type: none !important;

        .block-editor-inner-blocks {
            display: block;
            margin: 10px;
        }
    }

    .premium-content-switcher-labels-style {
        font-weight: 700;
        margin: 0;
        font-size: 12px;
        line-height: 1;
        padding: 0 0px 15px;
    }
}

/************ Responsive **************/
@media (min-width: 977px) {
    .premium-desktop-hidden {
        display: none !important;
    }
}

@media only screen and (max-width: 976px) and (min-width: 766px) {
    .premium-tablet-hidden {
        display: none !important;
    }
}

@media (max-width: 767px) {
    .premium-mobile-hidden {
        display: none !important;
    }
}